<template>
  <div>
    <div class="product-detail">
      <div class="p-line-left"></div>
      <div class="p-contain">
        <div class="p-contain-info">{{$t('sale.afterSale')}}</div>
      </div>
      <div class="p-info">售后服务与支持</div>
      <div class="p-line-right"></div>
    </div>
    <div class="sale-info-area">
      <div class="btn-area">
        <button :class="activeBtn === 'question' ? 'btn nmt' : 'btn'" @click="btnEvent('question')">常见问题</button>
        <button :class="activeBtn === 'service' ? 'btn nmt' : 'btn'" @click="btnEvent('service')">售后服务</button>
        <button :class="activeBtn === 'policy' ? 'btn nmt' : 'btn'" @click="btnEvent('policy')">售后政策</button>
      </div>
      <div class="m-sale-title">{{ mtitle }}</div>
      <div class="m-notice">售后服务与支持</div>
      <!-- 每一个做不同的块 -->
      <div class="sale-info">
        <div v-for="infos in information" :key="infos.id" :style=" infos.title ? 'margin-top: 14px;' : '' ">
          <div v-if="infos.title" style="line-height: 20px;">{{ infos.title }}</div>
          <div style="line-height: 20px;">{{ infos.contain }}</div>
        </div>
      </div>
    </div>
    <note-foot class="bg-gray"></note-foot>
  </div>
</template>

<script>
import NoteFoot from "@/components/NoteFoot.vue";
export default {
  components: {
    "note-foot": NoteFoot
  },
  data() {
    return {
      information: [],
      activeBtn: 'question',
      mtitle: '常见问题',
      question: [
        {
          id: 0,
          title: "1、笔记酱B1能打印多大尺寸的页面?",
          contain:
            " 笔记酱81是打印3寸的宽幅页面，即宽度达8厘米，长度根据纸卷厚度，最长可达19米，B1相比市面上常规的2寸打印机(宽度为5. 7厘米)，可实现更大宽幅打印。。"
        },
        {
          id: 1,
          title: "2、如何使用笔i2酱学生错题打印机?",
          contain:
            " 在本机App Store (苹果手机)或安卓市场(安卓手机》中搜索并下载“笔记酱”APP, 注册并登录后，使用蓝牙连接笔记酱，按APP提示进行使用，即可打印错题， 具体可参考笔己酱使用说明书。"
        },
        {
          id: 2,
          title: "3、笔记酱学生错题打印机需要购买墨水吗?",
          contain:
            "您不需要额外购买墨水、墨盒，碳带等常规打印机耗材，我们为方更您的使用，为该打印机研发了无墨、无毒的热敏打印技术，使用日本进口打印头，无需墨水，只需使用官方指定热敏打印机，即可实现打印。"
        },
        {
          id: 3,
          title: "4、笔记酱如何充电?",
          contain:
            "本机使用标准Micro USB 数据线充电，连接5V适配器可进行充电，或连接电脑USB口可直接充电。"
        },
        {
          id: 4,
          title: "5、笔记酱充电多久可充满?",
          contain:
            "笔记酱从安全和保护电池耐用程度，选择了更为安全的充电方案，从没电到充满大约需要2.5小时左右时间。"
        },
        {
          id: 5,
          title: "6、笔记酱指示灯提示",
          contain:
            "指示灯白灯常亮为正常工作状态，指示灯红灯常亮提示:缺纸、开盖、低电量、过热等情况。"
        },
        {
          id: 6,
          title: "7、笔记酱能否使用普通纸，进行打印?",
          contain: "笔记酱史用的是完全无墨化的热敏打印技术，需使用热敏纸才能进行正常打印，不支持普通纸打印，为了更长效使用笔记酱，建议使用笔记酱官方打印纸。"
        },
        {
          id: 7,
          title: "8、打印完成后可以用手触摸笔记酱打印头吗?",
          contain:
            "打印头工作温度达70'C左右，打印完成后禁止触摸打印头，笔记酱使用8本进口打印头，质量稳定、高效、耐用，同时笔记酱研发了打印头过热保护程序，当打印头问题过高时，机器会暂停工作，直至温度降至安全区间"
        },
        {
          id: 8,
          title: "9、笔记酱打印出来文本或图片可以保存多久?",
          contain:
            "为满足您的多样化需求，官方在售不同耗材可支持留影时长在1 ~20年不等区间，您可按需购买。"
        }
      ],
      service: [
        {
          id: 0,
          title: "笔记酱保修服务条例",
          contain:
            "消费者通过合法渠道购买的笔记酱，若出现非人为损坏的性能故障，产品售后服务将严格依据《中华人民共和国消费者权益保护法》、《中华人民 共和国产品质量法》实行“包修/包退/包换”(简称“三包”)的服务承诺，服务内容如下:"
        },
        {
          id: 1,
          title: "保修条例",
          contain:
            "1.自签收之日起15日内，本产品出现非人为损坏的性能故障，消费者可选择换货或退货;"
        },
        {
          id: 2,
          title: "",
          contain:
            "2.自签收之日起12个月内，本产品出现非人为损坏的性能故障，消费者享有免费保修服务。免费保修服务不包括保险、邮递、运送等杂费，且不提供上门服务"
        },
        {
          id: 3,
          title: "非保修条例",
          contain:
            "1.未经授权的维修/误用/滥用/进夜/事故/改动/不正确的使用，或撕毁/涂改标签"
        },
        {
          id: 4,
          title: "",
          contain:
            "2.人为原因造成，自然灾害或火灾等外部灾害成的损坏"
        },
        {
          id: 5,
          title: "",
          contain:
            "3.超过三包有效期"
        }
      ],
      policy: [
        {
          id: 0,
          title: "",
          contain:
            "消费者通过合法渠道购买的笔记酱，若出现非人为损坏的性能故障，产品售后服务将严格依据《中华人民共和国消费者权益保护法》、《中华人民共和国产品质量法》实行“包修/包退/包换” (简称“三包”)的服务承诺。"
        }
      ]
    }
  },
  watch: {
    $route(to, from) {
      const e = this.$route.query.type
      if(e === 'question'){
        this.information = this.question
        this.mtitle = '常见问题'
      }
      if(e === 'service'){
        this.information = this.service
        this.mtitle = '售后服务'
      }
      if(e === 'policy'){
        this.information = this.policy
        this.mtitle = '售后政策'
      }
    }
  },
  mounted() {
    this.information = this.question
  },
  methods: {
    btnEvent(e) {
      this.activeBtn = e
      if(e === 'question'){
        this.information = this.question
      }
      if(e === 'service'){
        this.information = this.service
      }
      if(e === 'policy'){
        this.information = this.policy
      }
    }
  }
};
</script>
<style scoped>
@media screen and (max-width: 1200px) {
  .product-detail {
    display: none;
  }
  .sale-info-area .btn-area {
    display: none;
  }

  .sale-info-area {
    background-color: #f6f6f6;
  }
  .sale-info-area .m-sale-title {
    height: 64px;
    padding-top: 40px;
    font-size: 34px;
    font-family: SimHei;
    font-weight: bold;
    color: #333333;
    text-align: center;
    line-height: 64px;
  }
  .sale-info-area .m-notice {
    margin: 30px 40px 0 40px;
    padding-bottom: 14px;
    font-size: 26px;
    color: #ccc;
    border-bottom: 1px solid #333;
  }
  .sale-info-area .sale-info {
    min-height: 100vh;
    padding: 40px;
    font-size: 28px;
    font-family: SimHei;
    font-weight: 400;
    color: #666666;
    line-height: 56px;
  }
}

@media screen and (min-width: 1199px) {
  .bg-gray {
    background-color: #dcdcdc;
  }
  .sale-info-area .m-sale-title {
    display: none;
  }
  .sale-info-area .m-notice { 
    display: none;
  }
  button {
    margin: 0;
    padding: 0;
    border: 1px solid transparent;
    background-color: transparent;
    outline: none;
  }
  .product-detail {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 162px;
    font-size: 28px;
    font-family: SimHei;
    color: #222222;
  }

  .product-detail .p-contain {
    flex: 2;
    display: flex;
    align-items: flex-end;
  }

  .product-detail .p-contain-info {
    margin: 0 auto;
    margin-bottom: 10px;
  }

  .product-detail .p-line-left {
    position: absolute;
    top: 80px;
    left: 40%;
    width: 112px;
    height: 1px;
    background-color: #d2d2d2;
  }
  .product-detail .p-line-right {
    position: absolute;
    top: 80px;
    right: 40%;
    width: 112px;
    height: 1px;
    background-color: #d2d2d2;
  }
  .product-detail .p-info {
    flex: 1;
    font-size: 16px;
    color: #777777;
    font-family: MyriadPro-Regular;
    font-weight: 400;
    text-align: center;
    /* line-height: 20px; */
  }
  .sale-info-area {
    display: flex;
    min-height: 800px;
    margin-top: 20px;
    margin-bottom: 100px;
  }
  .sale-info-area .btn-area {
    width: 24.69vw;
    height: 75.68vh;
  }

  .sale-info-area .btn-area .btn {
    width: 164px;
    height: 42px;
    cursor: pointer;
    margin-top: 60px;
    margin-left: 12.19vw;
    color: #515151;
    border: 1px solid #a0a0a0;
    border-radius: 4px;
  }
  .sale-info-area .btn-area .btn.nmt {
    color: #fff;
    background-color: #515151;
  }  
  .sale-info-area .btn-area .btn:hover {
    color: #ffffff;
    background-color: #515151;
  }
  .sale-info-area .sale-info {
    width: 63.96vw;
    height: 72.04vh;
    font-size: 18px;
    font-family: SimHei;
    font-weight: 400;
    color: #000000;
    line-height: 40px;
  }
}
</style>


