<template>
  <div class="head-area">
    <div class="left-item hide">
      <span @click="backIndex" style="cursor: pointer;">{{$t('head.title')}}</span>
    </div>
    <div class="right-item hide">
      <router-link to="/product" exact @click.native="linkActive(0)">
        {{$t('head.product')}}<div v-if="showShortLineIndex === 0" class="short-line-area">
          <div class="short-line"></div>
        </div>
      </router-link>
      <router-link to="/download" exact @click.native="linkActive(1)">
        {{$t('head.download')}}<div v-if="showShortLineIndex === 1" class="short-line-area">
          <div class="short-line"></div>
        </div>
      </router-link>
      <router-link to="/saleService" exact @click.native="linkActive(2)">
        {{$t('head.saleService')}}<div v-if="showShortLineIndex === 2" class="short-line-area">
          <div class="short-line"></div>
        </div>
      </router-link>
      <router-link to="/cooperate" exact @click.native="linkActive(3)">
        {{$t('foot.cooperate')}}<div v-if="showShortLineIndex === 3" class="short-line-area">
          <div class="short-line"></div>
        </div>
      </router-link>
      <!-- <router-link to="/language" exact>{{$t('head.lang')}}</router-link> -->
      <!-- <router-link class="right-up-item" to="/news" exact @click.native="linkActive(4)">
        {{$t('foot.news')}}<div v-if="showShortLineIndex === 4" class="short-line-area">
          <div class="short-line"></div>
        </div>
      </router-link> -->
      <!-- <router-link class="right-up-item" to="/cooperate" exact @click.native="linkActive(5)">
        {{$t('foot.cooperate')}}<div v-if="showShortLineIndex === 5" class="short-line-area">
          <div class="short-line"></div>
        </div>
      </router-link> -->
      <span @click="changeLang(language)">
        <!-- <a href='javascript:void(0)'></a> -->
        <a href="javascript:void(0)">{{$t('head.lang')}}</a>
      </span>
    </div>
    <div class="m-left-item">
      <div class="beekey-icon">
        <img src="../assets/img/mobile/m-beekey-logo.png" alt width="100%" height="100%" />
      </div>
    </div>
    <div class="m-right-item">
      <img
        class="menu-icon"
        src="../assets/img/mobile/seeting-icon.png"
        alt
        width="100%"
        height="100%"
        @click="showMenu"
      />
    </div>

    <!-- 移动端弹框 -->
    <div v-if="showMenuFlag" class="m-dialog">
      <div class="m-dialog-mask"></div>
      <div class="m-dialog-up">
        <router-link class="m-dialog-menu" to="/" @click.native="showMenuClose" exact>首页</router-link>
        <router-link
          class="m-dialog-menu"
          to="/product"
          @click.native="showMenuClose"
          exact
        >{{$t('head.product')}}</router-link>
        <!-- <router-link
          class="m-dialog-menu"
          to="/"
          @click.native="showMenuClose"
          exact
        >{{$t('head.shop')}}</router-link> -->
        <router-link
          class="m-dialog-menu"
          to="/download"
          @click.native="showMenuClose"
          exact
        >{{$t('head.download')}}</router-link>
        <!-- <router-link to="/saleService" exact>{{$t('head.saleService')}}</router-link> -->
        <div class="m-dialog-menu-last" @click="showSaleBtn">
          <div class="img-font">
            <div>售后</div>
          </div>
          <div class="img-area">
            <div class="img">
              <img src="../assets/img/mobile/down-icon.png" alt width="100%" height="100%" />
            </div>
          </div>
        </div>
      </div>
      <div class="m-dialog-down">
        <div v-if="showSaleBtnFlag" class="m-button-area">
          <router-link class="m-btn-item" :to="{path:'/saleService',query: {type: 'question'}}" @click.native="showMenuClose" exact>常见问题</router-link>
          <router-link class="m-btn-item" :to="{path:'/saleService',query: {type: 'service'}}" @click.native="showMenuClose" exact>售后服务</router-link>
          <router-link class="m-btn-item" :to="{path:'/saleService',query: {type: 'policy'}}" @click.native="showMenuClose" exact>售后政策</router-link>
        </div>
        <div class="m-none"></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "note-head",
  data() {
    return {
      language: "en",
      showMenuFlag: false,
      showSaleBtnFlag: false,
      showShortLineIndex: 0
    };
  },
  created() {},
  mounted() {
    const _routes = this.$router.options.routes;
    const now_path = this.$route.path;
    let _index = 0;
    _routes.forEach((v, i) => {
      if (v.path === now_path) {
        _index = i;
      }
    });
    this.linkActive(_index);
  },
  watch: {
    $route(to, from) {
      const _routes = this.$router.options.routes;
      const now_path = to.path;
      let _index = 0;
      _routes.forEach((v, i) => {
        if (v.path === now_path) {
          _index = i;
        }
      });
      this.linkActive(_index);
    }
  },
  methods: {
    changeLang(e) {
      if (e === "zh") {
        this.$i18n.locale = "zh";
        this.language = "en";
      } else if (e === "en") {
        this.$i18n.locale = "en";
        this.language = "zh";
      }
    },
    backIndex() {
      this.$router.push({
        path: "/"
      })
    },
    linkActive(e) {
      this.showShortLineIndex = e
    },
    showMenu() {
      this.showMenuFlag = !this.showMenuFlag;
      if (this.showMenuFlag) {
        var mo = function(e) {
          e.preventDefault();
        };
        document.body.style.overflow = "hidden";
        document.addEventListener("touchmove", mo, false); //禁止页面滑动
      } else {
        var mo = function(e) {
          e.preventDefault();
        };
        document.body.style.overflow = ""; //出现滚动条
        document.removeEventListener("touchmove", mo, false);
      }
    },
    showMenuClose() {
      this.showMenuFlag = false;
      var mo = function(e) {
        e.preventDefault();
      };
      document.body.style.overflow = ""; //出现滚动条
      document.removeEventListener("touchmove", mo, false);
    },
    showSaleBtn() {
      this.showSaleBtnFlag = !this.showSaleBtnFlag;
    }
  }
};
</script>

<style scoped>
@media screen and (max-width: 1200px) {
  /* 隐藏电脑的布局块 */
  .hide {
    display: none;
  }
  a {
    text-decoration: none;
  }
  a:hover {
    background: #4f6969;
  }

  .m-dialog {
    position: fixed;
    top: 88px;
    left: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: #000000;
    opacity: 0.7;
    z-index: 1;
  }
  .m-dialog .m-dialog-up {
    flex: 2;
    display: flex;
    flex-direction: column;
    padding: 30px 20px;
    /* background-color: #ffffff; */
    /* opacity: 0.5; */
    z-index: 2;
  }
  .m-dialog .m-dialog-up .m-dialog-menu {
    flex: 1;
    display: flex;
    align-items: center;
    padding: 0 40px;
    z-index: 3;
    font-size: 28px;
    font-family: SimHei;
    font-weight: 400;
    color: #ffffff;
    /* background: #4f6969; */
    border-radius: 6px;
  }
  .m-dialog .m-dialog-up .m-dialog-menu-last {
    flex: 1;
    display: flex;
    padding: 0 40px;
    z-index: 3;
    font-size: 28px;
    font-family: SimHei;
    font-weight: 400;
    color: #ffffff;
    border-radius: 6px;
  }
  .m-dialog .m-dialog-up .m-dialog-menu-last .img-font {
    flex: 1;
    display: flex;
    align-items: center;
  }
  .m-dialog .m-dialog-up .m-dialog-menu-last .img-area {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .m-dialog .m-dialog-up .m-dialog-menu-last .img-area .img {
    width: 40px;
    height: 40px;
  }

  .m-dialog .m-dialog-down {
    flex: 3;
    display: flex;
    flex-direction: column;
    padding: 0 40px;
  }
  .m-dialog .m-dialog-down .m-button-area {
    flex: 2;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 30px 40px;
    z-index: 5;
  }
  .m-dialog .m-dialog-down .m-button-area .m-btn-item {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 28px;
    font-family: SimHei;
    font-weight: 400;
    color: #ffffff;
    border-bottom: 1px solid #686868;
  }
  .m-dialog .m-dialog-down .m-none {
    flex: 2;
  }

  .head-area {
    position: relative;
    display: flex;
    width: 100%;
    height: 88px;
    line-height: 88px;
  }
  .head-area .m-left-item {
    flex: 1;
    display: flex;
    align-items: center;
    padding-left: 20px;
  }
  .head-area .m-left-item .beekey-icon {
    width: 160px;
    height: 68px;
  }
  .head-area .m-right-item {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 20px;
  }
  .head-area .m-right-item .menu-icon {
    width: 40px;
    height: 40px;
  }
}

@media screen and (min-width: 1199px) {
  .m-left-item {
    display: none;
  }
  .m-right-item {
    display: none;
  }
  /* button {
    margin: 0;
    padding: 0;
    border: 1px solid transparent;
    background-color: transparent;
    outline: none;
  } */
  .head-area {
    position: relative;
    display: flex;
    height: 88px;
    line-height: 88px;
  }
  .left-item {
    flex: 1;
    padding-left: 12.71%;
    font-size: 30px;
    font-family: SimHei;
    font-weight: 400;
    letter-spacing: 20px;
    text-align: left;
  }
  .left-item.nmt {
    letter-spacing: 20px;
  }
  .right-item {
    flex: 1;
    padding-right: 12.71%;
    font-size: 20px;
    font-family: SimHei;
    font-weight: 400;
    text-align: right;
  }
  a {
    padding: 0 8px;
    color: #010101;
    text-decoration: none;
  }
  a:hover {
    padding-bottom: 14px;
    color: #0ec0b1;
    /* border-bottom: 3px solid #0ec0b1; */
  }
  .router-link-exact-active {
    position: relative;
    padding-bottom: 14px;
    color: #0ec0b1;
    /* border-bottom: 3px solid #0ec0b1; */
  }
  /* 短线 */
  .short-line-area {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    /* display: flex; */
    /* justify-content: center; */
  }
  .short-line {
    margin: 0 auto;
    width: 40px;
    height: 3px;
    background: #0ec0b1;
    border-radius: 2px;
  }
}
</style>




