            <img src="../assets/img/wechat-icon.png" alt width="100%" height="100%" />
          </button>
          <button class="icon-area">
            <img src="../assets/img/weibo-icon.png" alt width="100%" height="100%" />
          </button>
          <button class="icon-area">
            <img src="../assets/img/qq-icon.png" alt width="100%" height="100%" />
          </button>
          <button class="icon-area">
            <img src="../assets/img/taobao-icon.png" alt width="100%" height="100%" />
          </button>
        </div>
        <div class="foot-left-down">Copyright © 2019 xiamen Janfo Co.,Ltd.</div>
      </div>
      <div class="foot-item">
        <div class="foot-right-up">
          <router-link class="right-up-item-f" to="/product" exact>{{$t('head.product')}}</router-link>
          <router-link class="right-up-item" to="/" exact>{{$t('head.shop')}}</router-link>
          <router-link class="right-up-item" to="/download" exact>{{$t('head.download')}}</router-link>
          <router-link class="right-up-item" to="/saleService" exact>{{$t('head.saleService')}}</router-link>
          <!-- <router-link class="right-up-item" to="/news" exact>{{$t('foot.news')}}</router-link> -->
          <router-link class="right-up-item" to="/cooperate" exact>{{$t('foot.cooperate')}}</router-link>
          <router-link class="right-up-item" to="/index" exact>{{$t('foot.about')}}</router-link>
        </div>
        <div class="foot-right-down">厦门简风科技有限公司</div>
      </div>
    </div>
    <!-- 移动端 -->
    <div class="m-foot-area">
      <div class="foot-one">
        <div>商务合作</div>
        <div style="font-size: 16px; margin-top: 6px;">180 0600 9650</div>
        <div style="font-size: 16px;">ken@janfo.com</div>
      </div>
      <div  class="foot-one">
        <img src="../assets/img/mobile/m-logo.png" alt="" width="120px" height="48px">
      </div>
      <div class="m-foot-down">Copyright © 2019 xiamen Janfo Co.,Ltd.</div>
    </div>
  </div>
</template>
<script>
export default {
  name: "note-foot"
};
</script>

<style scoped>
@media screen and (max-width: 1200px) {
  .foot-area {
    display: none;
  }

  .foot-one {
    width: 100%;
    height: 180px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #666;
    background-color: #202020;
  }
  .m-foot-down {
    width: 100%;
    height: 72px;
    color: #666;
    background-color: #000;
    text-align: center;
    line-height: 72px;
  }
}

@media screen and (min-width: 1199px) {
  .m-foot-area {
    display: none;
  }

  button {
    margin: 0;
    padding: 0;
    border: 1px solid transparent;
    background-color: transparent;
    outline: none;
  }
  a {
    color: #868585;
    text-decoration: none;
  }
  a:hover {
    color: #0ec0b1;
  }
  .foot-area {
    position: relative;
    display: flex;
    width: 100%;
    height: 74px;
    margin-bottom: 34px;
    padding: 72px 0;
  }
  .foot-area-black {
    position: relative;
    display: flex;
    width: 100%;
    height: 74px;
    margin-bottom: 34px;
    padding: 72px 0;
    background-color: #dcdcdc;
  }
  .foot-area .foot-item {
    flex: 1;
  }
  .foot-area .foot-left-up {
    display: flex;
    padding-left: 20%;
    font-size: 30px;
    font-family: SimHei;
    font-weight: 400;
    /* color: #010101; */
  }
  .foot-area .foot-left-up .icon-area-l {
    cursor: pointer;
    margin-top: 3px;
    margin-left: 40px;
    width: 30px;
    height: 30px;
  }
  .foot-area .foot-left-up .icon-area {
    cursor: pointer;
    margin-top: 3px;
    margin-left: 24px;
    width: 30px;
    height: 30px;
  }
  .foot-area .foot-left-down {
    margin-top: 14px;
    padding-left: 20%;
    font-size: 20px;
    font-family: SimHei;
    font-weight: 400;
    color: #9c9c9c;
