    font-size: 20px;
    font-family: SimHei;
    font-weight: 400;
    /* color: #868585; */
    text-align: center;
  }
  .foot-area .foot-right-up .right-up-item {
    flex: 1;
    font-size: 20px;
    font-family: SimHei;
    font-weight: 400;
    /* color: #868585; */
    border-left: 1px solid #868585;
    text-align: center;
  }
  .foot-area .foot-right-down {
    margin-top: 20px;
    padding-right: 20%;
