import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import NoteHead from '@/components/NoteHead'
import Index from '@/view/index'
import Product from '@/view/product'
import ProductDetail from '@/view/productDetail'
import Download from '@/view/download'
import SaleService from '@/view/saleService'
import Cooperate from '@/view/cooperate'
import News from '@/view/news'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/product',
      name: 'ProductDetail',
      component: ProductDetail
    },
    {
      path: '/download',
      name: 'Download',
      component: Download
    },
    {
      path: '/saleService',
      name: 'SaleService',
      component: SaleService
    },
    {
      path: '/cooperate',
      name: 'Cooperate',  
      component: Cooperate
    },
    {
      path: '/news',
      name: 'News',
      component: News
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/',
      name: 'Product',
      component: Product
    }
  ],
  // mode: 'history'
})



